<template>
  <main-layout>
    <div class="container">
      <div class="row">
        <div class="col-md-4 mx-auto">
          <div class="myform form">
            <form @submit.prevent="handleSubmit">
              <div class="form-group">
                <input
                  v-model="username"
                  type="text"
                  name="name"
                  class="form-control my-input"
                  id="name"
                  placeholder="Name"
                />
              </div>
              <br />
              <div class="form-group">
                <input
                  v-model="email"
                  type="email"
                  name="email"
                  class="form-control my-input"
                  id="email"
                  placeholder="Email"
                />
              </div>
              <br />
              <div class="form-group">
                <input
                  v-model="password"
                  type="password"
                  min="4"
                  name="password"
                  id="password"
                  class="form-control my-input"
                  placeholder="Password"
                />
              </div>
              <br />
              <div class="text-center">
                <button type="submit" class="btn btn-primary">
                  Create Your Free Account
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </main-layout>
</template>



<script>
import MainLayout from "../layouts/Main";
import axios from "axios";

export default {
  name: "Signup",
  data() {
    return {
      username: "",
      email: "",
      password: "",
      msg: ""
    };
  },
  components: {
    MainLayout,
  },
  methods: {
    handleSubmit() {
      const data = {
        username: this.username,
        email: this.email,
        password: this.password,
      };

      axios
        .post("http://website-walid.herokuapp.com/user/create", data, {
          "Content-Type": "application/x-www-form-urlencoded",
        })
        .then((res) => {
          console.log(res.response.data.msg);
          alert(res.response.data.msg);
        })
        .catch((err) => {
          this.msg = err.response.data.msg;
          console.log(err.response.data.msg);
         alert(this.msg)

        });
    },
  },
};
</script>